/* eslint-disable react/display-name */
import { Col, Form, Image, Row } from "antd";
import React, { memo, useContext, useEffect, useMemo, useState } from "react";
import logo from "../../../../assets/logos/impact-tms.png";
import DarkHeader from "../components/DarkHeader";
import SecondaryLabel from "../../../components/Labels/SecondaryLabel";
import {
  convertBlobToBase64,
  convertToFloat,
  dataURLtoFile,
  downloadBlobFile,
  stringIsNullOrEmpty,
  triggerNotification,
  valueToNumber,
} from "../../../../utilz/utilz";
import PrintInput from "../components/PrintInput";
import PrintSelectBox from "../components/PrintSelectBox";
import { getAllCountriesEffect } from "../../../../redux/common/CommonEffects";
import { sumBy } from "lodash";
import HiddenFormInput from "../../../components/Inputs/HiddenFormInput";
import { Document, pdf } from "@react-pdf/renderer";
import CIPrintDocument from "./components/CIPrintDocument";
import { formateWithMoment } from "../../../../utilz/dates";
import moment from "moment";
import {
  saveShipmentCommercialInvoiceEffect,
  shipmentFileUploadEffect,
} from "../../../../redux/shipment/ShipmentEffects";
import { saveAs } from "file-saver";
import { NOTIFICATION_TYPES } from "../../../../contants/common";
import { useSelector } from "react-redux";
import PrintContext from "./components/PrintContext";
import { useLocation } from "react-router-dom";
import { Trans, useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";
import ScrollPagination from "../../../components/Paginations/ScrollPagination";
import { wrap } from "comlink";
import { CI_SHIPMENT_TRANSLATIONS } from "../../../../contants/shipmentPrintsTranslations";

const worker = new Worker(
  new URL("../../../../workers/worker.js", import.meta.url),
  {
    name: "pdf-ci-worker",
    type: "module",
  }
);
const workerApi = wrap(worker);

const PrintShipmentCI = ({ ciDetails, userId }) => {
  const [form] = Form.useForm();
  const [countryList, setCountryList] = useState([]);
  const { notificationAPI } = useSelector((state) => state.common);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    if (countryList.length === 0) {
      (async () => {
        try {
          let { data } = await getAllCountriesEffect();
          data = data.map(({ countryCode, countryName }) => ({
            label: countryName,
            value: countryCode,
          }));
          setCountryList(data);
        } catch (error) {
          setCountryList([]);
        }
      })();
    }
  }, [countryList]);

  const ciData = useMemo(() => {
    let data = {};
    if (ciDetails) {
      data = {
        ...ciDetails?.commercialInvoiceRecord,
        products: ciDetails?.commercialInvoiceProducts
          ? ciDetails?.commercialInvoiceProducts
          : [],
      };
    }
    return data;
  }, [ciDetails]);

  useEffect(() => {
    if (ciData?.products) {
      form.setFieldsValue({
        products: ciData?.products,
        insurance: ciData?.insurance,
        rateShopCost: ciData?.rateShopCost,
        ciRefNo: ciData?.ciRefNo,
        releaseReferenceNo: ciData?.releaseReferenceNo,
        ciRefNoDate: formateWithMoment(ciData?.ciRefNoDate, "YYYY-MM-DD"),
        releaseReferenceNoDate: formateWithMoment(
          ciData?.releaseReferenceNoDate,
          "YYYY-MM-DD"
        ),
      });
      setTimeout(() => {
        calculateAmount();
      }, 500);
    }
  }, [ciData?.products]);

  const handleGenerate = ({ ...ciData }, isMail) => {
    return new Promise(async (resolve, reject) => {
      try {
        const formValues = form.getFieldsValue();
        await calculateAmount();
        let payload = {
          ciRefNo: formValues?.ciRefNo,
          shipmentkey: ciData?.shipmentKey,
          insurance: formValues?.insurance,
          releaseReferenceNo: formValues?.releaseReferenceNo,
          ciRefNoDate: !stringIsNullOrEmpty(formValues?.ciRefNoDate)
            ? formValues?.ciRefNoDate
            : null,
          releaseReferenceNoDate: !stringIsNullOrEmpty(
            formValues?.releaseReferenceNoDate
          )
            ? formValues?.releaseReferenceNoDate
            : null,
          productDetails: formValues.products.map((product) => ({
            shipmentDetailsID: product.shipmentDetailsID,
            hts: product.hts,
            eccn: product.eccn,
            countryOfOrigin: product.countryOfOrigin,
            unitPrice: product.unitPrice,
          })),
        };

        const { data: saveData } = await saveShipmentCommercialInvoiceEffect(
          payload
        );

        const fileName = `Commercial_Invoice_${
          ciData?.commercialInvoiceNo
        }_${moment().format("MMDDYYYY")}_${moment().format("HHmm")}.pdf`;

        const translations = await getTranslations(CI_SHIPMENT_TRANSLATIONS);

        const blob = await workerApi.generateCIPDF(
          JSON.stringify({
            ...ciData,
            ciRefNo: formValues?.ciRefNo,
            releaseReferenceNo: formValues?.releaseReferenceNo,
            insurance: formValues?.insurance,
            invoiceLineTotal: formValues?.invoiceLineTotal,
            totalInvoiceAmount: formValues?.totalInvoiceAmount,
            products: formValues?.products,
            ciRefNoDate: formateWithMoment(formValues?.ciRefNoDate),
            releaseReferenceNoDate: formateWithMoment(
              formValues?.releaseReferenceNoDate
            ),
            translations,
          })
        );

        const fileBase64 = await convertBlobToBase64(blob);
        var file = dataURLtoFile(fileBase64, fileName);
        if (!isMail) {
          const formData = new FormData();
          formData.append(
            "CustomerKey",
            localStorage.getItem("selectedCompany")
          );
          formData.append("ShipmentKey", ciData?.shipmentKey);
          formData.append("File", file);
          formData.append("CreatedBy", userId);
          formData.append("DocumentDesc", "Commercial Invoice");
          formData.append("DocumentTypeCode", "CI");
          formData.append("ReferenceNo", ciData?.shipmentNo);
          formData.append("DocumentTitle", fileName);

          const { data } = await shipmentFileUploadEffect(formData);
          downloadBlobFile(blob, fileName);
          triggerNotification(notificationAPI, {
            type: NOTIFICATION_TYPES.SUCCESS,
            title: t(
              "translation:default.fields.notification.successfulyGeneratedCommercial"
            ),
          });
        }
        resolve({ file, fileName });
      } catch (error) {
        reject(t("translation:default.fields.failedGgeneratePDF"));
        console.log(error);
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.ERROR,
          title: t(
            "translation:default.fields.notification.failedToGenerateCommercialInvoiceFile"
          ),
        });
      }
    });
  };

  const getTranslations = (labelObj) => {
    return new Promise((resolve, reject) => {
      try {
        let data = {};
        for (const key in labelObj) {
          if (Object.hasOwnProperty.call(labelObj, key)) {
            data[key] = t(labelObj[key]);
          }
        }
        resolve(data);
      } catch (error) {
        reject(error);
      }
    });
  };

  const calculateAmount = () => {
    return new Promise((resolve, reject) => {
      try {
        let products = form.getFieldValue("products");
        products = products.map(({ quantity, unitPrice, ...row }) => ({
          ...row,
          totalAmount: isNaN(unitPrice * quantity)
            ? 0
            : convertToFloat(unitPrice * quantity),
          unitPrice,
          quantity,
        }));
        let invoiceLineTotal = sumBy(products, ({ totalAmount }) =>
          Number(totalAmount)
        );
        form.setFieldsValue({ invoiceLineTotal, products });

        const insurance = form.getFieldValue("insurance");
        const rateShopCost = form.getFieldValue("rateShopCost");
        form.setFieldsValue({
          totalInvoiceAmount: convertToFloat(
            valueToNumber(invoiceLineTotal) +
              valueToNumber(insurance) +
              valueToNumber(rateShopCost)
          ),
        });
        resolve(true);
      } catch (error) {
        reject(error);
      }
    });
  };

  const { generatePDFRef } = useContext(PrintContext);

  useEffect(() => {
    generatePDFRef.current = {
      generate: handleGenerate,
      data: { ...ciData },
    };
  }, [generatePDFRef, ciData]);

  const invoiceLineTotal = Form.useWatch("invoiceLineTotal", form);
  const rateShopCost = Form.useWatch("rateShopCost", form);
  const totalInvoiceAmount = Form.useWatch("totalInvoiceAmount", form);

  return (
    <Form
      layout="vertical"
      defaultValue={{ products: [] }}
      form={form}
      className="d-flex flex-direction-column"
    >
      <div className="flex-1 background-white pb-20">
        <table className="print-table">
          <tbody>
            <tr>
              <td colSpan={11} className={"py-2"}>
                <Image
                  className="shipment-print-tms-logo"
                  preview={false}
                  src={logo}
                  alt="logo"
                />
              </td>
            </tr>
            <tr>
              <td colSpan={11} className={"py-5 px-10"}>
                <Row justify={"space-between"} align={"middle"}>
                  <Col className="lh-0">
                    <label className="f-10">
                      Date: {formateWithMoment(ciData?.shipDate)}
                    </label>
                  </Col>
                  <Col className="lh-0">
                    <label className="fw-700 f-14">
                      {t("translation:default.fields.commercialInvoice")}{" "}
                      {ciData?.commercialInvoiceNo}
                    </label>
                  </Col>
                  <Col className="lh-0">
                    <label className="fw-700 f-10">{`${t(
                      "translation:default.fields.page"
                    )} 1 ${t("translation:shipment.of")} 1 `}</label>
                  </Col>
                </Row>
              </td>
            </tr>
            <tr>
              <td colSpan={11} className="position-relative">
                <Row className="flex-nowrap">
                  <Col sm={12} className={"border-right-black"}>
                    <DarkHeader forceTop={true}>
                      {t("translation:default.fields.shipFrom")}
                    </DarkHeader>

                    <div className="mt-15 py-12 px-5">
                      <p className="mb-10 f-12 fw-700">
                        {t("translation:default.buttons.export")}{" "}
                        {t("translation:default.fields.identificationNumber")}{" "}
                        {ciData?.shipperEIN}
                      </p>
                      <p className="m-0 text-black f-12">
                        {ciData?.shipperName}
                        <br />
                        {ciData?.shipperAddress}
                        <br />
                        {ciData?.shipperCountryCode} <br />
                        {!stringIsNullOrEmpty(ciData?.shipperState) &&
                          ciData?.shipperState !== "NS" && (
                            <>
                              {ciData?.shipperState} <br />
                            </>
                          )}
                        {ciData?.shipperZip}, {ciData?.shipperCity}
                      </p>
                    </div>
                    <DarkHeader forceTop={false}>
                      {t("translation:default.fields.shipTo")}
                    </DarkHeader>

                    <div className="py-12 px-5">
                      <p className="mb-10 f-12 fw-700">
                        {t("translation:default.buttons.export")}{" "}
                        {t("translation:default.fields.identificationNumber")}{" "}
                        {ciData?.consigneeEIN}
                      </p>
                      <p className="m-0 text-black f-12">
                        {ciData?.consigneeName}
                        <br />
                        {ciData?.consigneeAddress}
                        <br />
                        {ciData?.consigneeCountryCode} <br />
                        {!stringIsNullOrEmpty(ciData?.consigneeState) &&
                          ciData?.consigneeState !== "NS" && (
                            <>
                              {ciData?.consigneeState} <br />
                            </>
                          )}
                        {ciData?.consigneeZip}, {ciData?.consigneeCity}
                      </p>
                    </div>
                  </Col>
                  <Col sm={12}>
                    <div className="py-10 px-5">
                      <Row gutter={30}>
                        <Col sm={12} className={"mb-15"}>
                          <PrintInput
                            fullBorder={true}
                            name="ciRefNo"
                            withLabel={true}
                            label={t(
                              "translation:default.documentManager.referenceNo.title"
                            )}
                          />
                        </Col>
                        <Col sm={12} className={"mb-15"}>
                          <PrintInput
                            fullBorder={true}
                            name="releaseReferenceNo"
                            withLabel={true}
                            label={t(
                              "translation:default.fields.customRelease"
                            )}
                          />
                        </Col>
                        <Col sm={12} className={"mb-10"}>
                          <PrintInput
                            fullBorder={true}
                            name="ciRefNoDate"
                            withLabel={true}
                            label={t(
                              "translation:default.fields.referenceDate"
                            )}
                            type={"date"}
                          />
                        </Col>
                        <Col sm={12} className={"mb-10"}>
                          <PrintInput
                            fullBorder={true}
                            name="releaseReferenceNoDate"
                            withLabel={true}
                            label={t("translation:default.fields.releaseDate")}
                            type={"date"}
                          />
                        </Col>
                      </Row>
                    </div>
                    <DarkHeader forceTop={false}>
                      {t("translation:default.fields.soldTo")}
                    </DarkHeader>

                    <div className="py-10 px-5">
                      <p className="mb-10 f-12 fw-700">
                        {t("translation:default.buttons.export")}{" "}
                        {t("translation:default.fields.identificationNumber")}{" "}
                        {ciData?.ultimateDestEIN}
                      </p>
                      {!stringIsNullOrEmpty(ciData?.saiName) &&
                      !stringIsNullOrEmpty(ciData?.saiAddress) ? (
                        <>
                          <p className="m-0 text-black f-12">
                            {ciData?.saiName}
                            <br />
                            {ciData?.saiAddress}
                            <br />
                            {ciData?.saiCountryCode} <br />
                            {!stringIsNullOrEmpty(ciData?.saiState) &&
                              ciData?.saiState !== "NS" && (
                                <>
                                  {ciData?.saiState} <br />
                                </>
                              )}
                            {ciData?.saiZip}, {ciData?.saiCity}
                          </p>
                        </>
                      ) : (
                        <>
                          <p className="m-0 text-black f-12">
                            {ciData?.consigneeName}
                            <br />
                            {ciData?.consigneeAddress}
                            <br />
                            {ciData?.consigneeCountryCode} <br />
                            {!stringIsNullOrEmpty(ciData?.consigneeState) &&
                              ciData?.consigneeState !== "NS" && (
                                <>
                                  {ciData?.consigneeState} <br />
                                </>
                              )}
                            {ciData?.consigneeZip}, {ciData?.consigneeCity}
                          </p>
                        </>
                      )}
                    </div>
                  </Col>
                </Row>
              </td>
            </tr>
            <tr>
              <td colSpan={11}>
                <Row gutter={5} className="border-bottom-black">
                  <Col className="w-10 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700 ">
                      {t("translation:default.fields.productCode")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-25 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.productDesc.label")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-10 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.htsCode")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-10 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.eccn")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-6 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.coo")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-6 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.license")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-5 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.quantity.label")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-8 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.quantityUOM")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-5 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.unitPrice")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-9 text-center border-right-black">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.totalAmount")}
                    </SecondaryLabel>
                  </Col>
                  <Col className="w-6 text-center">
                    <SecondaryLabel className="f-12 fw-700">
                      {t("translation:default.fields.currency.label")}
                    </SecondaryLabel>
                  </Col>
                </Row>
                <Form.List name={"products"}>
                  {(fields) => {
                    return fields?.length > 0 ? (
                      <ScrollPagination
                        className="custom-scroll-bar shipment-print-product-height virtualized-print-shipment-list"
                        isVirtualizationApproach={true}
                        isLoading={false}
                        hasMore={true}
                        scrollOffset={0}
                        extraHeight={-1}
                      >
                        {fields.map(({ name }) => {
                          return (
                            <ProductRow
                              key={name}
                              name={name}
                              calculateAmount={calculateAmount}
                              ciData={ciData}
                              countryList={countryList}
                            />
                          );
                        })}
                      </ScrollPagination>
                    ) : (
                      <></>
                    );
                  }}
                </Form.List>
              </td>
            </tr>
            <tr>
              <td colSpan={11} className={"py-20 px-50"}>
                <Row justify={"end"}>
                  <Col>
                    <Row gutter={15} className={"mb-5"}>
                      <Col>
                        <label className="f-14 fw-700">
                          {t("translation:default.fields.invoiceLine")}
                        </label>
                      </Col>
                      <Col>
                        <label className="f-14 ">
                          {convertToFloat(invoiceLineTotal)}{" "}
                          {ciData?.currencyCode}
                        </label>
                      </Col>
                    </Row>
                    <Row gutter={15} className={"mb-5"}>
                      <Col>
                        <label className="f-14 fw-700">
                          {t("translation:default.fields.insurance")}
                        </label>
                      </Col>
                      <Col>
                        <Row gutter={10}>
                          <Col>
                            <PrintInput
                              name={"insurance"}
                              type={"number"}
                              className={"f-14"}
                              onBlur={calculateAmount}
                            />
                          </Col>
                          <Col>{ciData?.currencyCode}</Col>
                        </Row>
                      </Col>
                    </Row>
                    <Row gutter={15} className={"mb-5"}>
                      <Col>
                        <label className="f-14 fw-700">
                          {t("translation:default.fields.freight")}
                        </label>
                      </Col>
                      <Col>
                        <label className="f-14 ">
                          {convertToFloat(rateShopCost)} {ciData?.currencyCode}
                        </label>
                      </Col>
                    </Row>
                    <Row gutter={15} className={"mb-5"}>
                      <Col>
                        <label className="f-14 fw-700">
                          {t("translation:default.fields.totalInvoice")}
                        </label>
                      </Col>
                      <Col>
                        <label className="f-14 ">
                          {convertToFloat(totalInvoiceAmount)}{" "}
                          {ciData?.currencyCode}
                        </label>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </td>
            </tr>
            <tr>
              <td className="px-10" colSpan={11}>
                <br />
                <br />
                <p className="m-0 f-12 fw-700">
                  {t("translation:default.fields.legalText")}
                </p>
                <br />
                <br />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <HiddenFormInput name="invoiceLineTotal" />
      <HiddenFormInput name="rateShopCost" />
      <HiddenFormInput name="totalInvoiceAmount" />
      {/* <div className="pdfViewer">
        <PDFViewer>
          <MyPDFDocument
            ciData={{
              ...ciData,
              ciRefNo,
              releaseReferenceNo,
              ciRefNoDate: formateWithMoment(ciRefNoDate),
              releaseReferenceNoDate: formateWithMoment(releaseReferenceNoDate),
              insurance,
              invoiceLineTotal,
              totalInvoiceAmount,
              products,
            }}
          />
        </PDFViewer>
      </div> */}
    </Form>
  );
};

const ProductRow = memo(({ name, calculateAmount, ciData, countryList }) => {
  return (
    <Row gutter={5} className="border-bottom-black w-100">
      <Col className="w-10 f-12 py-2 px-5 border-right-black">
        {ciData?.products[name]?.productCode}
      </Col>
      <Col className="w-25 f-12 py-2 px-5 border-right-black">
        {ciData?.products[name]?.productDescription}
      </Col>
      <Col className="w-11 f-12 py-2 px-5 border-right-black">
        <PrintInput onBlur={calculateAmount} name={[name, "hts"]} />
      </Col>
      <Col className="w-10 f-12 py-2 px-5 border-right-black">
        <PrintInput onBlur={calculateAmount} name={[name, "eccn"]} />
      </Col>
      <Col className="w-6 f-12 py-2 px-5 border-right-black">
        <PrintSelectBox
          name={[name, "countryOfOrigin"]}
          options={countryList}
        />
      </Col>
      <Col className="w-6 f-12 py-2 px-5 border-right-black">
        {ciData?.products[name]?.license}
      </Col>
      <Col className="w-5 f-12 py-2 px-5 border-right-black">
        {convertToFloat(ciData?.products[name]?.quantity)}
      </Col>
      <Col className="w-8 f-12 py-2 px-5 border-right-black">
        {ciData?.products[name]?.unitMeasure}
      </Col>
      <Col className="w-5 f-12 py-2 px-5 border-right-black">
        <PrintInput
          onBlur={calculateAmount}
          name={[name, "unitPrice"]}
          type={"number"}
        />
      </Col>
      <Col className="w-9 f-12 py-2 px-5 border-right-black">
        <PrintInput
          onBlur={calculateAmount}
          name={[name, "totalAmount"]}
          type={"number"}
          readOnly={true}
        />
      </Col>
      <Col className="w-5 f-12 py-5">{ciData?.currencyCode}</Col>
    </Row>
  );
});

export default PrintShipmentCI;

const MyPDFDocument = ({ ciData }) => {
  return (
    <Document>
      <CIPrintDocument ciData={ciData} />
    </Document>
  );
};
