import { Document } from "@react-pdf/renderer";
import React, { useMemo, useState } from "react";
import { convertDateStringtoMoment } from "../../../../../utilz/dates";
import { removeNAfromObjectValues } from "../../../../../utilz/utilz";
import BolPrintDocument from "./BolPrintDocument";
import CIPrintDocument from "./CIPrintDocument";
import PLPrintDocument from "./PLPrintDocument";

export const ShipmentPrintAllDocuments = ({
  ciDetails = {},
  plDetails = {},
  bolDetails = {},
  shipmentDetails = {},
  barCodeImage,
}) => {
  const [inputData, setInputData] = useState({
    consigneeLocation: "",
    bolNo: "",
    agreedValue1: "",
    agreedValue2: "",
    codAmount: "",
    freeTermsCollect: "",
    freeTermsPrePaid: "",
    freeTermsCustomerCheque: "",
    trailerByShipper: "",
    trailerByDriver: "",
    freightByShipper: "",
    freightByDriverPallets: "",
    freightByDriverPieces: "",
    masterBillOfLadingAttached: false,
    // shipperSignature: "",
  });
  const plData = useMemo(() => {
    let data = {};
    if (plDetails) {
      data = {
        ...plDetails?.packingListRecord,
        products: plDetails?.packingListProducts
          ? plDetails?.packingListProducts
          : [],
      };
    }
    return data;
  }, [plDetails]);

  const ciData = useMemo(() => {
    let data = {};
    if (ciDetails) {
      data = {
        ...ciDetails?.commercialInvoiceRecord,
        products: ciDetails?.commercialInvoiceProducts
          ? ciDetails?.commercialInvoiceProducts
          : [],
      };
    }
    return data;
  }, [ciDetails]);

  const bolData = useMemo(() => {
    let shipmentDate = convertDateStringtoMoment(shipmentDetails?.shipmentDate);
    shipmentDate = shipmentDate ? shipmentDate.format("MM/DD/YYYY") : "";
    const bolPdfData = bolDetails?.bolPdfInputs?.[0]
      ? removeNAfromObjectValues(bolDetails?.bolPdfInputs?.[0])
      : null;
    setInputData({
      ...inputData,
      consigneeLocation: bolPdfData?.consigneeLocation,
      masterBillOfLadingAttached: bolPdfData?.masterBillOfLadingAttached,
    });
    return {
      ...shipmentDetails,
      ...inputData,
      shipmentDate,
      items: bolDetails?.bolItem ? bolDetails?.bolItem : [],
      bolPdfData,
      consigneeLocation: bolPdfData?.consigneeLocation,
    };
  }, [bolDetails]);

  return (
    <Document>
      <CIPrintDocument ciData={ciData} />
      <PLPrintDocument plData={plData} />
      <BolPrintDocument
        bolData={bolData}
        inputData={inputData}
        barCodeImage={barCodeImage}
      />
    </Document>
  );
};
