import { Col, Form, Row, Spin } from "antd";
import React, { useEffect, useRef, useState, useMemo } from "react";
import {
  SHIPMENT_PRINTS_DOC_TYPES,
  SHIPMENT_PRINTS_TAB_ID,
} from "../../../../contants/ShipmentEnums";
import IconButton from "../../../components/Buttons/IconButton";
import SvgConverter from "../../../components/Common/SvgConverter";
import Tab from "../../../components/Tabs/Tab";
import { ReactComponent as DownloadICon } from "../../../../assets/icons/download-white.svg";
import { ReactComponent as PrintICon } from "../../../../assets/icons/print.svg";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import PrintShipmentCI from "./PrintShipmentCI";
import { useDispatch, useSelector } from "react-redux";
import {
  getShipmentBolEffect,
  getShipmentCommercialInvoiceEffect,
  getShipmentPackingListEffect,
} from "../../../../redux/shipment/ShipmentEffects";
import {
  booleanToNumber,
  downloadBlobFile,
  triggerNotification,
} from "../../../../utilz/utilz";
import { NOTIFICATION_TYPES } from "../../../../contants/common";
import PrintContext from "./components/PrintContext";
import PrintShipmentPackingList from "./PrintShipmentPackingList";
import PrintShipmentViewBol from "./PrintShipmentViewBol";
import { ShipmentPrintAllDocuments } from "./components/ShipmentPrintAllDocuments";
import Barcode from "react-barcode";
import html2canvas from "html2canvas";
import { pdf, PDFViewer } from "@react-pdf/renderer";
import { saveAs } from "file-saver";
import moment from "moment";
import FormLabel from "../../../components/Labels/FormLabel";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import FormInput from "../../../components/Inputs/FormInput";
import Button from "../../../components/Buttons/Button";
import { sendMailShipmentDocumentInProgress } from "../../../../redux/shipment/ShipmentActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";
import PrintShipmentViewImo from "./PrintShipmentViewImo";
import PrintShipmentViewSED from "./PrintShipmentViewSED";
import PrintShipmentViewCMR from "./PrintShipmentViewCMR";
import { useDebounce } from "use-debounce";
import ContentSpinner from "../../../components/Spinners/ContentSpinner";

const ShipmentPrintTabs = ({
  printActiveTab,
  setPrintActiveTab,
  zoomValue = 1,
  printModalOpen = false,
}) => {
  const [printModalOpenDeBounce] = useDebounce(printModalOpen, 800);
  return (
    <>
      {printModalOpenDeBounce ? (
        <PrintTabs
          printActiveTab={printActiveTab}
          setPrintActiveTab={setPrintActiveTab}
          zoomValue={zoomValue}
        />
      ) : (
        <ContentSpinner size="large" />
      )}
    </>
  );
};

const PrintTabs = ({ printActiveTab, setPrintActiveTab, zoomValue = 1 }) => {
  const { user } = useSelector((state) => state.auth);
  const { notificationAPI, carrierDetails } = useSelector(
    (state) => state.common
  );
  const {
    shipmentDetails,
    sendDocumentMail,
    shipmentProductFullList,
    documentDetails,
  } = useSelector((state) => state.shipment);
  const dispatch = useDispatch();
  const [ciDetails, setCIDetails] = useState({});
  const [ciDetailsLoading, setCIDetailsLoading] = useState(false);
  const [PLDetails, setPLDetails] = useState({});
  const [PLDetailsLoading, setPLDetailsLoading] = useState(false);
  const [BOLDetails, setBOLDetails] = useState({});
  const [BOLDetailsLoading, setBOLDetailsLoading] = useState(false);
  const [generatingPDF, setGeneratingPDF] = useState(false);
  const generatePDFRef = useRef({ generate: false, data: false });
  const [barCodeImage, setBarCodeImage] = useState(false);
  const [mailForm] = Form.useForm();
  const [documentMailLoading, setDocumentMailLoading] = useState(false);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const contents = [
    {
      title: t("translation:default.fields.commercialInvoiceContents"),
      content: <></>,
      contentKey: SHIPMENT_PRINTS_TAB_ID.COMMERCIAL_INVOICE,
    },
    {
      title: t("translation:default.fields.packingListContents"),
      content: <></>,
      contentKey: SHIPMENT_PRINTS_TAB_ID.PACKING_LIST,
    },
    {
      title: t("translation:default.fields.billOfLandingContent"),
      content: <></>,
      contentKey: SHIPMENT_PRINTS_TAB_ID.BOL,
    },
    {
      title: "View IMO",
      content: <></>,
      contentKey: SHIPMENT_PRINTS_TAB_ID.IMO,
    },
    {
      title: "View CMR",
      content: <></>,
      contentKey: SHIPMENT_PRINTS_TAB_ID.CMR,
    },
    {
      title: "View SED",
      content: <></>,
      contentKey: SHIPMENT_PRINTS_TAB_ID.SED,
    },
  ];

  useEffect(() => {
    if (shipmentDetails?.data?.shipmentKey) {
      fetchCI(shipmentDetails?.data?.shipmentKey);
      fetchPL(shipmentDetails?.data?.shipmentKey);
      fetchBOL(shipmentDetails?.data?.shipmentKey);
    }
  }, [shipmentDetails?.success]);

  const fetchCI = async (shipmentKey) => {
    try {
      setCIDetailsLoading(true);
      const { data } = await getShipmentCommercialInvoiceEffect({
        shipmentKey,
        customerKey: localStorage.getItem("selectedCompany"),
      });
      setCIDetails(data);
      setCIDetailsLoading(false);
    } catch (error) {
      console.log(error);
      setCIDetailsLoading(false);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.fields.dataFetchingError"),
      });
    }
  };

  const fetchPL = async (shipmentKey) => {
    try {
      setPLDetailsLoading(true);
      const { data } = await getShipmentPackingListEffect({
        shipmentKey,
        customerKey: localStorage.getItem("selectedCompany"),
      });
      setPLDetails(data);
      setPLDetailsLoading(false);
    } catch (error) {
      console.log(error);
      setPLDetailsLoading(false);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.fields.dataFetchingError"),
      });
    }
  };

  const fetchBOL = async (shipmentKey) => {
    try {
      setBOLDetailsLoading(true);
      const { data } = await getShipmentBolEffect({
        shipmentKey,
        customerKey: localStorage.getItem("selectedCompany"),
      });
      setBOLDetails(data);
      setBOLDetailsLoading(false);
    } catch (error) {
      console.log(error);
      setBOLDetailsLoading(false);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.fields.dataFetchingError"),
      });
    }
  };

  const generatePDF = async (type) => {
    try {
      setGeneratingPDF(true);
      const data = await generatePDFRef.current.generate(
        generatePDFRef.current.data
      );
      if (type === "PRINT") {
        var file = new Blob([data.file], { type: "application/pdf" });
        var fileURL = URL.createObjectURL(file);
        window.open(fileURL, "_blank");
      }
      setGeneratingPDF(false);
    } catch (error) {
      console.log(error);
      setGeneratingPDF(false);
      alert("Failed");
    }
  };

  useEffect(() => {
    if (shipmentDetails?.data?.shipmentKey) {
      const element = document.querySelector("#shipmentBarCode .print-section");
      (async () => {
        let canvas = await html2canvas(element, {
          //   width: 290,
          //   height: 50,
        });
        setBarCodeImage(canvas.toDataURL("image/png"));
      })();
    }
  }, [shipmentDetails]);

  const generateAllDocuments = async () => {
    try {
      const fileName = `Combine_${
        shipmentDetails?.data?.shipmentNo
          ? shipmentDetails?.data?.shipmentNo
          : ""
      }_${moment().format("MMDDYYYY")}_${moment().format("HHmm")}.pdf`;
      const blob = await pdf(
        <ShipmentPrintAllDocuments
          ciDetails={ciDetails}
          plDetails={PLDetails}
          bolDetails={BOLDetails}
          shipmentDetails={shipmentDetails?.data}
          barCodeImage={barCodeImage}
        />
      ).toBlob();
      downloadBlobFile(blob, fileName);
      var file = new Blob([blob], { type: "application/pdf" });
      var fileURL = URL.createObjectURL(file);
      window.open(fileURL, "_blank");
    } catch (error) {}
  };

  const handleEmail = async (data) => {
    try {
      setDocumentMailLoading(true);
      let params = {
        shipmentKey: shipmentDetails?.data?.shipmentKey,
        customEmailId: data?.customEmailId,
        self: booleanToNumber(data?.self),
        carrier: booleanToNumber(data?.carrier),
        iscustom: booleanToNumber(data?.iscustom),
        DocumentType: SHIPMENT_PRINTS_DOC_TYPES[printActiveTab],
      };

      const formData = new FormData();
      const fileData = await generatePDFRef.current.generate(
        generatePDFRef.current.data,
        true
      );
      formData.append("file", fileData?.file);
      dispatch(sendMailShipmentDocumentInProgress({ params, data: formData }));
      setDocumentMailLoading(false);
    } catch (error) {
      console.log(error);
      setDocumentMailLoading(false);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.fields.failedToSendDocumentEmail"),
      });
    }
  };
  const mailFormValues = Form.useWatch(null, mailForm);
  return (
    <PrintContext.Provider value={{ generatePDFRef }}>
      <div className="h-100 d-flex flex-direction-column">
        <div className="d-flex">
          <div className="flex-1">
            <Tab
              onChange={(key) => setPrintActiveTab(key)}
              contents={contents}
              // defaultActiveKey={"1"}
              activeKey={printActiveTab}
            />
          </div>
          <div className="pr-15">
            <Spin
              wrapperClassName="h-100 d-flex"
              spinning={
                generatingPDF ||
                ciDetailsLoading ||
                PLDetailsLoading ||
                BOLDetailsLoading ||
                shipmentProductFullList?.progressing ||
                documentDetails?.progressing ||
                carrierDetails?.progressing
              }
            >
              <Row
                gutter={15}
                align={"middle"}
                className={" border-bottom-tab-border h-100"}
              >
                <Col>
                  <IconButton onClick={() => generatePDF("DOWNLOAD")}>
                    <SvgConverter
                      Icon={DownloadICon}
                      color={"header"}
                      width={20}
                      height={20}
                    />
                  </IconButton>
                </Col>
                <Col>
                  <IconButton onClick={() => generatePDF("PRINT")}>
                    <SvgConverter
                      Icon={PrintICon}
                      color={"header"}
                      width={20}
                      height={20}
                    />
                  </IconButton>
                </Col>
                {printActiveTab != "4" ? (
                  <Col>
                    <IconButton onClick={generateAllDocuments}>
                      <PrimaryLabel className={"fw-400 text-underline f-16"}>
                        {t("translation:default.fields.generateAllSupporting")}
                      </PrimaryLabel>
                    </IconButton>
                  </Col>
                ) : (
                  <></>
                )}
              </Row>
            </Spin>
          </div>
        </div>

        <div className="py-15 flex-1 custom-scroll-bar horizontal">
          <div
            className="px-15"
            style={{
              transform: `scale(${zoomValue})`,
              transformOrigin: "0% 0%",
            }}
          >
            {SHIPMENT_PRINTS_TAB_ID.COMMERCIAL_INVOICE === printActiveTab && (
              <Spin spinning={ciDetailsLoading}>
                <PrintShipmentCI
                  userId={user?.data?.id}
                  ciDetails={ciDetails}
                />
              </Spin>
            )}
            {SHIPMENT_PRINTS_TAB_ID.PACKING_LIST === printActiveTab && (
              <Spin spinning={PLDetailsLoading}>
                <PrintShipmentPackingList
                  userId={user?.data?.id}
                  packageListDetails={PLDetails}
                />
              </Spin>
            )}
            {SHIPMENT_PRINTS_TAB_ID.BOL === printActiveTab && (
              <Spin spinning={BOLDetailsLoading}>
                <PrintShipmentViewBol
                  userId={user?.data?.id}
                  bolDetails={BOLDetails}
                />
              </Spin>
            )}
            {SHIPMENT_PRINTS_TAB_ID.IMO === printActiveTab && (
              <PrintShipmentViewImo shipmentDetails={shipmentDetails?.data} />
            )}
            {SHIPMENT_PRINTS_TAB_ID.SED === printActiveTab && (
              <PrintShipmentViewSED shipmentDetails={shipmentDetails?.data} />
            )}
            {SHIPMENT_PRINTS_TAB_ID.CMR === printActiveTab && (
              <PrintShipmentViewCMR shipmentDetails={shipmentDetails?.data} />
            )}
          </div>
        </div>
        {printActiveTab != "4" ? (
          <div className="p-15 border-top-tab-border">
            <Form form={mailForm} onFinish={handleEmail}>
              <Row gutter={15} align={"middle"} className={"flex-nowrap"}>
                <Col>
                  <FormLabel>
                    {t("translation:default.fields.sendCopyTo")}
                  </FormLabel>
                </Col>
                <Col>
                  <FormCheckBox
                    className={"m-0"}
                    checkBoxClass="normal-checkbox"
                    name={"self"}
                    label={
                      <PrimaryLabel className={"f-14 text-dark"}>
                        {t("translation:default.fields.myEmailAddress")}
                      </PrimaryLabel>
                    }
                  />
                </Col>
                <Col>
                  <FormCheckBox
                    className={"m-0"}
                    checkBoxClass="normal-checkbox"
                    name={"carrier"}
                    label={
                      <PrimaryLabel className={"f-14 text-dark"}>
                        {t(
                          "translation:default.fields.transportationProvider.label"
                        )}
                      </PrimaryLabel>
                    }
                  />
                </Col>
                <Col>
                  <FormCheckBox
                    className={"m-0"}
                    name={"iscustom"}
                    checkBoxClass="normal-checkbox"
                    label={
                      <PrimaryLabel className={"f-14 text-dark"}>
                        {t("translation:default.fields.otherEmailAddress")}
                      </PrimaryLabel>
                    }
                  />
                </Col>
                <Col className="flex-1">
                  {mailFormValues?.iscustom && (
                    <FormInput
                      name="customEmailId"
                      label={false}
                      editMode={true}
                      margin={"m-0"}
                      placeholder={t(
                        "translation:default.fields.typeEmailComma"
                      )}
                    />
                  )}
                </Col>
              </Row>
              <Button
                loading={sendDocumentMail?.progressing || documentMailLoading}
                className="mt-10"
                buttonType="submit"
                disabled={
                  !mailFormValues ||
                  !Object.values(mailFormValues).includes(true) ||
                  ciDetailsLoading ||
                  PLDetailsLoading ||
                  BOLDetailsLoading
                }
              >
                <span className="f-16">
                  {t("translation:default.buttons.sendEmail")}
                </span>
              </Button>
            </Form>
          </div>
        ) : (
          <div className="vh-2 w-100"></div>
        )}
      </div>
      <Row
        style={{ position: "absolute", zIndex: -99 }}
        className="flex-1 "
        justify="center"
        align={"middle"}
      >
        <Col className="barcode-container" id="shipmentBarCode">
          {shipmentDetails?.data?.shipmentKey ? (
            <div>
              <div className="print-section">
                <Barcode
                  height={40}
                  width={3}
                  displayValue={false}
                  value={shipmentDetails?.data?.proNo}
                />
              </div>
              <p className="text-center mb-0 fw-600 f-30 mt-n15">
                {shipmentDetails?.data?.proNo}
              </p>
            </div>
          ) : (
            ""
          )}
        </Col>
      </Row>

      {/* <div className="d-none">
        <div className="pdfViewer">
          <PDFViewer>
            <ShipmentPrintAllDocuments
              ciDetails={ciDetails}
              plDetails={PLDetails}
              bolDetails={BOLDetails}
              shipmentDetails={shipmentDetails?.data}
              barCodeImage={barCodeImage}
            />
          </PDFViewer>
        </div>
      </div> */}
    </PrintContext.Provider>
  );
};

export default ShipmentPrintTabs;
