import React, { useEffect } from "react";
import SecondaryLabel from "../../../../components/Labels/SecondaryLabel";
import PrintCheckBox from "../../components/PrintCheckBox";

const GenerateBolFeeTerms = ({
  label = "label",
  name = "name",
  onChange = () => {},
  checked = false,
  dataValue,
  isReverse = false,
}) => {
  useEffect(() => {
    if (dataValue == 0) {
      onChange({ target: { checked: false, name } });
    } else if (dataValue == 1) {
      onChange({ target: { checked: true, name } });
    }
  }, [dataValue]);

  return (
    <PrintCheckBox
      isReverse={isReverse}
      checked={checked}
      onChange={onChange}
      name={name}
    >
      <SecondaryLabel
        className={`f-10 white-space-nowrap ${!isReverse ? "fw-700" : ""}`}
      >
        {label}
        {isReverse ? "" : ":"}
      </SecondaryLabel>
    </PrintCheckBox>
  );
};

export default GenerateBolFeeTerms;
