import { Form, Input, Select } from "antd";
import React from "react";

const PrintSelectBox = ({
  className = "",
  name,
  onChange = () => {},
  value,
  readOnly = false,
  withLabel,
  label = "label",
  inputMode = "text",
  type = "text",
  fullBorder = false,
  options = [],
}) => {
  return (
    <Form.Item
      className="m-0 print-form-input"
      name={name}
      label={withLabel ? <label className="f-10">{label}</label> : false}
      labelCol={10}
      colon={false}
    >
      <Select
        size={"small"}
        className={`${className} print-input-box ${
          fullBorder ? "full-border" : ""
        }`}
        onChange={onChange}
        value={value}
        readOnly={readOnly}
        inputMode={inputMode}
        type={type}
        showSearch={true}
        {...{
          optionFilterProp: "children",
          filterOption: (input, option) =>
            option?.children?.toLowerCase()?.includes(input?.toLowerCase()),
        }}
      >
        {options.map(({ label, value }) => (
          <Select.Option
            className={"print-select-options"}
            value={value}
            key={value}
          >
            {label}
          </Select.Option>
        ))}
      </Select>
    </Form.Item>
  );
};

export default PrintSelectBox;
