export default PrintShipmentViewBol;

const ThirdPartyShipmentTerms = ({
  customerKey,
  shipmentTerms,
  customerBillToName,
  customerBillToAdd,
  brokerageCustomer = null, // It is not required So keep as null for now
  customerBillToCountry,
  customerBillToCity,
  customerBillToState,
  customerBillToPostal,
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const data = useMemo(
    () =>
      shipmentPdfThirdPartyUtils({
        customerKey,
        shipmentTerms,
        customerBillToName,
        customerBillToAdd,
        brokerageCustomer,
        customerBillToCountry,
        customerBillToCity,
        customerBillToState,
        customerBillToPostal,
      }),
    [
      customerKey,
      shipmentTerms,
      customerBillToName,
      customerBillToAdd,
      brokerageCustomer,
      customerBillToCountry,
      customerBillToCity,
      customerBillToState,
      customerBillToPostal,
    ]
  );

  return (
    <table className="border-none w-100">
      <tr>
        <td className="lh-10">
          <SecondaryLabel className="f-10">
            {t("translation:default.fields.name")}: {data.name}
          </SecondaryLabel>
        </td>
      </tr>
      <tr>
        <td className="lh-10">
          <SecondaryLabel className="f-10">
            {t("translation:default.fields.address.label")}: {data.address1}
          </SecondaryLabel>
        </td>
      </tr>
      <tr>
        <td className="lh-10">
          <SecondaryLabel className="f-10">
            {data?.address2Label
              ? data?.address2Label
              : "Country/City/State/Zip"}
            : {data.address2}
          </SecondaryLabel>
        </td>
      </tr>
    </table>
  );
};

const CodSection = ({ bolPdfData, handleDataChanges = () => {} }) => {
  const [inputValues, setInputValues] = useState({
    agreedValue1: "",
    agreedValue2: "",
    codAmount: "",
    freeTermsCollect: "",
    freeTermsPrePaid: "",
    freeTermsCustomerCheque: "",
  });
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const data = useMemo(() => {
    let data = {};
    if (bolPdfData == null) {
      data = {
        agreedValue1: "",
        agreedValue2: "",
      };
    } else {
      if (bolPdfData?.agreedValue1 == "" && bolPdfData?.agreedValue2 == "") {
        data = {
          agreedValue1: "",
          agreedValue2: "",
        };
      } else {
        data = {
          agreedValue1: bolPdfData?.agreedValue1,
          agreedValue2: bolPdfData?.agreedValue1,
          showInput: true,
        };
      }
    }
    if (bolPdfData?.codAmount > -1) {
      data.codAmount = bolPdfData?.codAmount;
    } else {
      data.codAmount = "";
    }
    setInputValues({
      agreedValue1: data?.agreedValue1,
      agreedValue2: data?.agreedValue2,
      codAmount: data?.codAmount,
    });
    return data;
  }, [bolPdfData]);
  const handleChange = (e) => {
    setInputValues({ ...inputValues, [e.target.name]: e.target.value });
  };

  const handleCheckboxChange = (e) => {
    setInputValues({ ...inputValues, [e.target.name]: e.target.checked });
  };

  useEffect(() => {
    handleDataChanges(inputValues);
  }, [inputValues]);

  return (
    <>
      <td className="px-5 py-3 btn">
        <p className="f-9">
          {t("translation:default.fields.whereTheRate")}
          <br />
          {t("translation:default.fields.agreedOrDeclared")}
          <br />
        </p>
        <SecondaryLabel className="d-flex f-9 align-items-center d-block w-70">
          <PrintInput
            className={"f-9"}
            onChange={handleChange}
            name={"agreedValue1"}
            value={inputValues?.agreedValue1}
          />
          <span className="mx-5">per</span>
          <PrintInput
            onChange={handleChange}
            name={"agreedValue2"}
            value={inputValues?.agreedValue2}
          />
        </SecondaryLabel>
      </td>
      <td className="px-5 py-5 btn">
        <Row className="flex-direction-column">
          <Col>
            <SecondaryLabel className="f-12 fw-700 d-flex px-30">
              <span className="white-space-nowrap mr-5">
                {t("translation:shipment.codAmount")} $
              </span>
              {bolPdfData?.codAmount > -1 ? (
                <u>{inputValues?.codAmount}</u>
              ) : (
                <PrintInput
                  onChange={handleChange}
                  name={"codAmount"}
                  value={inputValues?.codAmount}
                  className={"f-12 "}
                  maxLength={"9"}
                />
              )}
            </SecondaryLabel>
          </Col>
          <Col className="mt-5">
            <Row gutter={10} className="flex-nowrap">
              <Col>
                <SecondaryLabel className="f-12 fw-700 d-flex px-10">
                  <span className="white-space-nowrap mr-5">
                    {t("translation:default.fields.weightTable")}
                  </span>
                </SecondaryLabel>
              </Col>
              <Col>
                <Row gutter={30}>
                  <Col>
                    <GenerateBolFeeTerms
                      label={t(
                        "translation:default.fields.shipmentTerms.option_c"
                      )}
                      name="freeTermsCollect"
                      onChange={handleCheckboxChange}
                      checked={inputValues?.freeTermsCollect}
                      dataValue={bolPdfData?.freeTermsCollect}
                    />
                  </Col>
                  <Col>
                    <GenerateBolFeeTerms
                      label={t(
                        "translation:default.fields.shipmentTerms.option_p"
                      )}
                      name="freeTermsPrePaid"
                      onChange={handleCheckboxChange}
                      checked={inputValues?.freeTermsPrePaid}
                      dataValue={bolPdfData?.freeTermsPrePaid}
                    />
                  </Col>
                  <Col>
                    <GenerateBolFeeTerms
                      label={t("translation:shipment.customerCheck")}
                      name="freeTermsCustomerCheque"
                      onChange={handleCheckboxChange}
                      checked={inputValues?.freeTermsCustomerCheque}
                      dataValue={bolPdfData?.freeTermsCustomerCheque}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
        </Row>
      </td>
    </>
  );
};

