import { Col, Image, Row } from "antd";
import React, { useContext, useEffect, useMemo, useState } from "react";
import logo from "../../../../assets/logos/impact-tms.png";
import DarkHeader from "../components/DarkHeader";
import SecondaryLabel from "../../../components/Labels/SecondaryLabel";
import {
  convertBlobToBase64,
  convertToFloat,
  dataURLtoFile,
  downloadBlobFile,
  stringIsNullOrEmpty,
  triggerNotification,
  valueToNumber,
} from "../../../../utilz/utilz";
import { sumBy, uniq } from "lodash";
import { Document, pdf, PDFViewer } from "@react-pdf/renderer";
import { formateWithMoment } from "../../../../utilz/dates";
import moment from "moment";
import { shipmentFileUploadEffect } from "../../../../redux/shipment/ShipmentEffects";
import { saveAs } from "file-saver";
import { NOTIFICATION_TYPES } from "../../../../contants/common";
import { useSelector } from "react-redux";
import PLPrintDocument from "./components/PLPrintDocument";
import PrintContext from "./components/PrintContext";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";
import VirtualizedTable from "../../../components/Tables/VirtualizedTable";
import { wrap } from "comlink";
import { PL_SHIPMENT_TRANSLATIONS } from "../../../../contants/shipmentPrintsTranslations";

const worker = new Worker(
  new URL("../../../../workers/worker.js", import.meta.url),
  {
    name: "pdf-pl-worker",
    type: "module",
  }
);
const workerApi = wrap(worker);

const PrintShipmentPackingList = ({ packageListDetails, userId }) => {
  const { notificationAPI } = useSelector((state) => state.common);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const plData = useMemo(() => {
    let data = {};
    if (packageListDetails) {
      data = {
        ...packageListDetails?.packingListRecord,
        products: packageListDetails?.packingListProducts
          ? packageListDetails?.packingListProducts
          : [],
      };
    }
    return data;
  }, [packageListDetails]);

  const getTranslations = (labelObj) => {
    return new Promise((resolve, reject) => {
      try {
        let data = {};
        for (const key in labelObj) {
          if (Object.hasOwnProperty.call(labelObj, key)) {
            data[key] = t(labelObj[key]);
          }
        }
        resolve(data);
      } catch (error) {
        reject(error);
      }
    });
  };

  const handleGenerate = (plData, isMail) => {
    return new Promise(async (resolve, reject) => {
      try {
        const fileName = `Packing_List_${plData?.shipmentNo}_${moment().format(
          "MMDDYYYY"
        )}_${moment().format("HHmm")}.pdf`;

        const translations = await getTranslations(PL_SHIPMENT_TRANSLATIONS);
        const blob = await workerApi.generatePLPDF(
          JSON.stringify({
            ...plData,
            translations,
          })
        );
        const fileBase64 = await convertBlobToBase64(blob);

        var file = dataURLtoFile(fileBase64, fileName);
        if (!isMail) {
          const formData = new FormData();
          formData.append(
            "CustomerKey",
            localStorage.getItem("selectedCompany")
          );
          formData.append("ShipmentKey", plData?.shipmentKey);
          formData.append("File", file);
          formData.append("CreatedBy", userId);
          formData.append("DocumentDesc", "Packing List");
          formData.append("DocumentTypeCode", "PL");
          formData.append("ReferenceNo", plData?.shipmentNo);
          formData.append("DocumentTitle", fileName);

          const { data } = await shipmentFileUploadEffect(formData);
          downloadBlobFile(blob, fileName);
          triggerNotification(notificationAPI, {
            type: NOTIFICATION_TYPES.SUCCESS,
            title: t("translation:default.fields.generatedPackingList"),
          });
        }
        resolve({ file, fileName });
      } catch (error) {
        reject(t("translation:default.notifications.failedGgeneratePDF"));
        console.log(error);
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.ERROR,
          title: t("translation:default.fields.failedToGenerate"),
        });
      }
    });
  };

  const { generatePDFRef } = useContext(PrintContext);
  useEffect(() => {
    generatePDFRef.current = {
      generate: handleGenerate,
      data: plData,
    };
  }, [generatePDFRef, plData]);

  const TableHeader = () => (
    <thead>
      <tr>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.poNumber")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.lineNo")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.productCode.label")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.productDesc.label")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.packageID.label")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.packageQty")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.totalWeight")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.totalItemQty")}
          </SecondaryLabel>
        </td>
        <td colSpan={3} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.packageDimensions")}
          </SecondaryLabel>
        </td>
        <td rowSpan={2} className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.cube.label")}
          </SecondaryLabel>
        </td>
      </tr>
      <tr>
        <td className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">L</SecondaryLabel>
        </td>
        <td className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">W</SecondaryLabel>
        </td>
        <td className="text-center vertical-middle px-10">
          <SecondaryLabel className="f-12 fw-700">H</SecondaryLabel>
        </td>
      </tr>
    </thead>
  );

  const tableBody = () => {
    return ({ index, style }) => {
      return (
        <tr {...style}>
          <td className="f-12 p-5 vertical-middle">
            {plData.products[index].poNumber}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {plData.products[index].lineNos}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {plData.products[index].productCode}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {plData.products[index].productDescription}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {plData.products[index].packageId}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {convertToFloat(plData.products[index].billQty)}{" "}
            {plData.products[index].billQtyUOMDesc}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {convertToFloat(plData.products[index].weight)}{" "}
            {plData.products[index].weightUOM}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {convertToFloat(plData.products[index].quantity)}{" "}
            {plData.products[index].unitMeasure}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {convertToFloat(plData.products[index].length)}{" "}
            {plData.products[index].dimUOM}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {convertToFloat(plData.products[index].width)}{" "}
            {plData.products[index].dimUOM}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {convertToFloat(plData.products[index].height)}{" "}
            {plData.products[index].dimUOM}
          </td>
          <td className="f-12 p-5 vertical-middle">
            {convertToFloat(plData.products[index].cube)}{" "}
            {plData.products[index].dimUOMDesc}
          </td>
        </tr>
      );
    };
  };

  const TableFooter = () => (
    <tfoot>
      <tr>
        <td
          colSpan={5}
          className="f-12 p-5 vertical-middle"
          style={{ textAlign: "right" }}
        >
          <SecondaryLabel className="f-12 fw-700">
            {t("translation:default.fields.totals")}
          </SecondaryLabel>
        </td>
        <td className="f-12 p-5 vertical-middle">
          {convertToFloat(
            sumBy(plData?.products, ({ billQty }) => valueToNumber(billQty))
          )}{" "}
          {uniq(
            plData?.products.map(({ billQtyUOMDesc }) => billQtyUOMDesc)
          ).join(", ")}
        </td>
        <td className="f-12 p-5 vertical-middle">
          {convertToFloat(
            sumBy(plData?.products, ({ weight }) => valueToNumber(weight))
          )}{" "}
          {uniq(plData?.products.map(({ weightUOM }) => weightUOM)).join(", ")}
        </td>
        <td colSpan={4} className="f-12 p-5 vertical-middle">
          {convertToFloat(
            sumBy(plData?.products, ({ quantity }) => valueToNumber(quantity))
          )}{" "}
          {uniq(plData?.products.map(({ unitMeasure }) => unitMeasure)).join(
            ", "
          )}
        </td>
        <td className="f-12 p-5 vertical-middle">
          {convertToFloat(
            sumBy(plData?.products, ({ cube }) => valueToNumber(cube))
          )}{" "}
          {uniq(plData?.products.map(({ dimUOMDesc }) => dimUOMDesc)).join(
            ", "
          )}
        </td>
      </tr>
    </tfoot>
  );

  return (
    <div className=" h-100 overflow-hidden d-flex flex-direction-column">
      <div className="flex-1 custom-scroll-bar background-white pb-20">
        <table className="print-table">
          <tbody>
            <tr>
              <td colSpan={12} className={"py-2"}>
                <Image
                  className="shipment-print-tms-logo"
                  preview={false}
                  src={logo}
                  alt="logo"
                />
              </td>
            </tr>
            <tr>
              <td colSpan={12} className={"py-5 px-10"}>
                <Row justify={"space-between"} align={"middle"}>
                  <Col className="lh-0">
                    <label className="f-10">
                      {t("translation:default.date")}:{" "}
                      {formateWithMoment(plData?.shipdate)}
                    </label>
                  </Col>
                  <Col className="lh-0">
                    <label className="fw-700 f-14">
                      {t("translation:default.fields.packingList")}{" "}
                      {plData?.shipmentNo}
                    </label>
                  </Col>
                  <Col className="lh-0">
                    <label className="fw-700 f-10">{`${t(
                      "translation:default.fields.page"
                    )} 1 ${t("translation:shipment.of")} 1`}</label>
                  </Col>
                </Row>
              </td>
            </tr>
            <tr>
              <td colSpan={12} className="position-relative">
                <Row className="flex-nowrap">
                  <Col sm={8} className={"border-right-black"}>
                    <DarkHeader forceTop={true}>
                      {t("translation:default.fields.shipFrom")}
                    </DarkHeader>

                    <div className="mt-15 py-12 px-5">
                      <p className="m-0 text-black f-12">
                        {plData?.shipperName}
                        <br />
                        {plData?.shipperAddress}
                        <br />
                        {plData?.shipperCountryCode} <br />
                        {!stringIsNullOrEmpty(plData?.shipperState) &&
                          plData?.shipperState !== "NS" && (
                            <>
                              {plData?.shipperState} <br />
                            </>
                          )}
                        {plData?.shipperZip}, {plData?.shipperCity}
                      </p>
                    </div>
                  </Col>

                  <Col sm={8} className={"border-right-black"}>
                    <DarkHeader forceTop={true}>
                      {t("translation:default.fields.billTo")}
                    </DarkHeader>

                    <div className="mt-15 py-12 px-5">
                      <p className="m-0 text-black f-12">
                        {!stringIsNullOrEmpty(plData?.billToName) &&
                        !stringIsNullOrEmpty(plData?.billToCountryCode) ? (
                          <>
                            {plData?.billToName}
                            <br />
                            {plData?.billToAddress}
                            <br />
                            {plData?.billToCountryCode} <br />
                            {!stringIsNullOrEmpty(plData?.billToState) &&
                              plData?.billToState !== "NS" && (
                                <>
                                  {plData?.billToState} <br />
                                </>
                              )}
                            {plData?.billToZip}, {plData?.billToCity}
                          </>
                        ) : (
                          <>
                            {plData?.consigneeName}
                            <br />
                            {plData?.consigneeAddress}
                            <br />
                            {plData?.consigneeCountryCode} <br />
                            {!stringIsNullOrEmpty(plData?.consigneeState) &&
                              plData?.consigneeState !== "NS" && (
                                <>
                                  {plData?.consigneeState} <br />
                                </>
                              )}
                            {plData?.consigneeZip}, {plData?.consigneeCity}
                          </>
                        )}
                      </p>
                    </div>
                  </Col>
                  <Col sm={8} className={""}>
                    <DarkHeader forceTop={true}>
                      {t("translation:default.fields.supplierAddress")}
                    </DarkHeader>

                    <div className="mt-15 py-12 px-5">
                      <p className="m-0 text-black f-12">
                        {plData?.vendorName}
                        <br />
                        {plData?.vendorAddress}
                        <br />
                        {plData?.vendorCountryCode} <br />
                        {!stringIsNullOrEmpty(plData?.vendorState) &&
                          plData?.vendorState !== "NS" && (
                            <>
                              {plData?.vendorState} <br />
                            </>
                          )}
                        {plData?.vendorZip}, {plData?.vendorCity}
                      </p>
                    </div>
                  </Col>
                </Row>
              </td>
            </tr>
            <tr>
              <td colSpan={12}>
                <div className="w-100 virtualize-table-scroll-bar">
                  <VirtualizedTable
                    tableClassName="print-table"
                    header={<TableHeader />}
                    height={300}
                    itemCount={
                      plData?.products?.length ? plData.products.length : 0
                    }
                    itemSize={26}
                    row={tableBody}
                    footer={<TableFooter />}
                  />
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      {/* <div className="pdfViewer">
        <PDFViewer>
          <PLPrintDocument plData={plData} />
        </PDFViewer>
      </div> */}
    </div>
  );
};

export default PrintShipmentPackingList;

const MyPDFDocument = ({ plData }) => {
  return (
    <Document>
      <PLPrintDocument plData={plData} />
    </Document>
  );
};
