import { Form, Input } from "antd";
import React from "react";

const PrintInput = ({
  className = "",
  name,
  onChange = () => {},
  value,
  readOnly = false,
  withLabel = false,
  label = "label",
  inputMode = "text",
  type = "text",
  fullBorder = false,
  styles = {},
  rules = [],
  min = 0,
  maxLength = false,
  onBlur = () => {},
}) => {
  return (
    <Form.Item
      className="m-0 print-form-input"
      name={name}
      label={withLabel ? <label className="f-10">{label}</label> : false}
      labelCol={10}
      colon={false}
      rules={rules}
    >
      <Input
        name={name}
        min={min}
        className={`${className} print-input-box ${
          fullBorder ? "full-border" : ""
        }`}
        onChange={onChange}
        value={value}
        readOnly={readOnly}
        inputMode={inputMode}
        type={type}
        style={styles}
        maxLength={maxLength}
        onBlur={onBlur}
      />
    </Form.Item>
  );
};

export default PrintInput;
